@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rouble";
  font-display: swap;
  src: url("../fonts/rouble.woff2") format("woff2"), url("../fonts/rouble.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "RobotoCondensed";
  font-display: swap;
  src: url("../fonts/Roboto-Condensed.woff2") format("woff2"), url("../fonts/Roboto-Condensed.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "RobotoCondensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "RobotoCondensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "AlumniSans";
  font-display: swap;
  src: url("../fonts/AlumniSans-SemiBold.woff2") format("woff2"), url("../fonts/AlumniSans-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "AlumniSans";
  font-display: swap;
  src: url("../fonts/AlumniSans-Bold.woff2") format("woff2"), url("../fonts/AlumniSans-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "AlumniSans";
  font-display: swap;
  src: url("../fonts/AlumniSans-ExtraBold.woff2") format("woff2"), url("../fonts/AlumniSans-ExtraBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #e4f6f4;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  padding: 25px 0;
}
.header .container {
  display: flex;
  justify-content: center;
}
.header__logo {
  margin: 0 25px 0 0;
}
.header__title {
  margin: 0;
  color: #3abca7;
}
.header h1 {
  margin: 0;
  font-weight: 600;
  font-size: 60px;
  line-height: normal;
  font-family: "AlumniSans", "Arial", sans-serif;
  color: #3abca7;
}
.header p {
  margin: 0;
  color: #e18c25;
  word-wrap: break-word;
}
@media (max-width: 767px) {
  .header {
    padding: 15px 0;
  }
  .header__logo {
    margin: 0 15px 0 0;
    width: 80px;
    height: 80px;
  }
  .header h1 {
    font-size: 40px;
  }
  .header p {
    font-size: 16px;
  }
}

.to-do {
  padding: 25px 0;
}
@media (max-width: 767px) {
  .to-do {
    padding: 15px 0;
  }
}

.to-do__list-wrapper {
  max-width: 800px;
  min-height: 200px;
  padding: 25px;
  margin: 0 auto 25px auto;
  border: 5px solid #3abca7;
  border-radius: 25px;
  background-color: #ffffff;
  box-shadow: 10px 10px 30px 0 rgba(0, 0, 0, 0.75);
}

.to-do__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.to-do__task {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  margin: 0;
  border-bottom: 1px solid #3abca7;
}
.to-do__task:last-child {
  border-bottom: none;
}
.to-do__task--complete p {
  text-decoration: line-through;
}
.to-do__task--complete .to-do__complete {
  display: none;
}

.to-do__task p {
  word-wrap: break-word;
  margin: 0 15px 0 0;
}
@media (max-width: 767px) {
  .to-do__task p {
    font-size: 16px;
  }
}

.to-do__task button {
  border-radius: 15px;
  border: 1px solid #3abca7;
  background-color: #e4f6f4;
  box-shadow: inset rgba(0, 0, 0, 0.3) 0 -5px 10px;
  width: 70px;
  aspect-ratio: 1;
}
.to-do__task button.to-do__complete {
  margin: 0 15px 0 auto;
}
@media (hover: hover) {
  .to-do__task button:hover:not(.focus-visible) {
    box-shadow: inset rgba(0, 0, 0, 0.3) 0 0 10px;
    color: #3abca7;
    opacity: 0.5;
  }
}
.to-do__task button.focus-visible:focus {
  box-shadow: inset rgba(0, 0, 0, 0.3) 0 0 10px;
  color: #3abca7;
  opacity: 0.5;
}
@media (max-width: 767px) {
  .to-do__task button {
    width: 35px;
    border-radius: 10px;
    padding: 5px 5px 0 5px;
  }
  .to-do__task button.to-do__complete {
    margin: 0 7px 0 auto;
  }
  .to-do__task button svg {
    width: 22px;
    height: 22px;
  }
}

.to-do form {
  max-width: 800px;
  padding: 25px;
  margin: 0 auto 25px auto;
  border: 5px solid #3abca7;
  border-radius: 25px;
  background-color: #ffffff;
  box-shadow: 10px 10px 30px 0 rgba(0, 0, 0, 0.75);
  display: flex;
  flex-wrap: wrap;
}
.to-do form label {
  width: 100%;
  margin: 0 0 25px 0;
}
@media (max-width: 767px) {
  .to-do form label {
    font-size: 16px;
    margin: 0 0 16px;
  }
}
.to-do form input {
  width: 100%;
  margin: 0 0 25px 0;
  padding: 10px 25px;
  border: 1px solid #3abca7;
  border-radius: 10px;
}
@media (max-width: 767px) {
  .to-do form input {
    border-radius: 7px;
    padding: 7px 15px;
    margin: 0 0 16px;
    font-size: 16px;
  }
}
.to-do form button {
  border-radius: 15px;
  border: 1px solid #3abca7;
  background-color: #e4f6f4;
  box-shadow: inset rgba(0, 0, 0, 0.3) 0 -5px 10px;
  color: #e18c25;
  text-align: center;
  width: 150px;
  padding: 20px;
  margin: 0 0 0 auto;
  word-wrap: break-word;
  transition: 0.3s ease;
}
@media (hover: hover) {
  .to-do form button:hover:not(.focus-visible) {
    box-shadow: inset rgba(0, 0, 0, 0.3) 0 0 10px;
    color: #3abca7;
    opacity: 0.5;
  }
}
.to-do form button.focus-visible:focus {
  box-shadow: inset rgba(0, 0, 0, 0.3) 0 0 10px;
  color: #3abca7;
  opacity: 0.5;
}
@media (max-width: 767px) {
  .to-do form button {
    border-radius: 10px;
    width: 100px;
    padding: 10px;
    font-size: 16px;
  }
}

.footer {
  padding: 25px 0;
}
@media (max-width: 767px) {
  .footer {
    padding: 15px 0;
  }
}

.social-list {
  align-content: center;
  list-style-type: none;
  margin: 0 auto;
  padding: 0;
  display: flex;
  justify-content: left;
  flex-wrap: wrap;
  min-width: 108px;
  max-width: 165px;
}
@media (max-width: 767px) {
  .social-list {
    min-width: 40px;
    max-width: 120px;
    padding: 5px;
  }
}

.social-list__link {
  display: block;
  max-height: 54px;
  max-width: 54px;
  padding: 7px;
  transition: 0.3s ease;
}
@media (hover: hover) {
  .social-list__link:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
.social-list__link.focus-visible:focus {
  opacity: 0.5;
}
@media (max-width: 767px) {
  .social-list__link {
    max-height: 40px;
    max-width: 40px;
    padding: 5px;
  }
  .social-list__link svg {
    width: 30px;
    height: 30px;
  }
}

